body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

button,
input {
  border: none;
  background-image: none;
  background-color: transparent;
  box-shadow: none;
  font-family: "Roboto", sans-serif;
}

.App {
  display: flex;
  padding: 20px;
}

@media only screen and (max-width: 550px) {
  .App {
    flex-direction: column;
  }
}

.card {
  text-align: justify;
  float: left;
  height: 500px;
  border: 2px solid orangered;
  width: 23%;
  margin-right: 1.5%;
  margin: 1%;
  border-radius: 4px;
  padding: 8px;
  cursor: pointer;
  -webkit-transition: 0.2s all;
  transition: 0.2s all;
}

@media only screen and (max-width: 1000px) {
  .card {
    width: 47%;
  }
}

@media only screen and (max-width: 1000px) {
  .card {
    width: 97%;
    margin-right: 1.5%;
    margin-left: 1.5%;
  }
}

.card:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  border: 2px solid yellow;
}

.img-wrapper {
  position: relative;
  display: flex;
  max-width: 100%;
}

.card__img {
  object-fit: cover;
  max-width: 100%;
  width: 100%;
  border-radius: 4px;
}

.card__icon {
  font-size: 24px;
  color: pink;
  position: absolute;
  top: 10px;
  right: 10px;
  -webkit-transition: 0.2s all;
  transition: 0.2s all;
}

.card__icon:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.card__icon--red {
  color: red;
}

.card-list {
  width: 70%;
  flex-wrap: wrap;
  border: 3px solid yellowgreen;
  border-radius: 4px;
  justify-content: center;
  clear: both;
}


@media only screen and (max-width: 550px) {
  .card-list {
    width: 100%;
  }
}
.filters {
  margin-right: 40px;
  width: 25%;
}

@media only screen and (max-width: 550px) {
  .filters {
    width: 100%;
    margin-right: 0;
    margin-bottom: 15px;
  }
}

.filters__form {
  position: -webkit-sticky;
  position: sticky;
  padding-top: 10px;
  top: 0;
}
.filters__item {
  margin: 5px;
}

.btn {
  position: relative;
  top: 0;
  line-height: 36px;
  width: 78px;
  height: 36px;
  cursor: pointer;
  border-radius: 3px;
  margin: 0 7px 11px 0;
  background-color: #bc1939;
  box-shadow: 0px 5px #cf6076;
  color: #ffffff;
  font-size: 16px;
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.btn:hover {
  background: #25bd17;
  box-shadow: 0px 4px #53a157;
}

.btn:active {
  box-shadow: 0px 0px #717a33;
  top: 4px;
}

input[type="text"],
select {
  margin-bottom: 10px;
  width: 100%;
  max-width: 300px;
  padding: 12px 0;
  padding-left: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
  resize: vertical;
}

